import Vue from 'vue'
import VueRouter from 'vue-router'

import MainPage from '@/components/frame/MainPage'
import Home from '@/components/home/index'
import Particulars from '@/components/particulars/particulars'
import Works from '@/components/works/works'
import WorksPage from '@/components/works/worksPage'
import CompanyProfile from '@/components/works/companyProfile'
import Login from '@/components/login/login'
import myCenter from '@/components/myCenter/myCenter'

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'history',
  base: __dirname,
  linkActiveClass: 'active',
  routes: [
    {
      path: '/',
      component: MainPage,
      children: [
        { path: '/',
          name: '',
          component: Home
        },
        { path: '/particulars', name: '', component: Particulars },
        { path: '/works', name: '', component: Works },
        { path: '/worksPage', name: '', component: WorksPage },
        { path: '/companyProfile', name: '', component: CompanyProfile },
        { path: '/login', name: '', component: Login },
        { path: '/myCenter', name: '', component: myCenter },
      ]
    }
  ]
})

export default router



